export const menuPageDishImage = graphql`
  fragment menuPageDishImage on File {
    childImageSharp {
      fluid {
        ...GatsbyImageSharpFluid
      }
    }
  }
`
function LightPrice({ value, className, ...props }) {
  return <span className={`font-weight-light ${className}`} {...props}>{value.toFixed(2)}</span>;
}

const MenuPage = () => (
  <Layout>
    <SEO title="Menu" />
    <h1 className="display-1">Menu</h1>
    <p className="lead">Our standard menu available for delivery or pick-up at the store around dinner time.</p>

    <Row>
      <Col md={{ span: 8, offset: 2 }}>
        <Alert variant="info">
          Apart from our standard menu, we also provide complete <Link to="/hefan">lunch and dinner meal-boxes</Link>.
          We constantly vary these meals and announce them one day ahead through WeChat and WhatsApp.
        </Alert>
      </Col>
    </Row>

    <h2 className="mt-5">Main Dishes</h2>
    <Row>
      {dishes.filter(({ category }) => category === Category.MAIN).map(dish => (
        <Col key={dish.title} className="mt-3 mb-3" lg={4} md={6}>
          <Dish {...dish} />
        </Col>
      ))}
    </Row>

    <h2 className="mt-5">Side Dishes</h2>
    <Row>
      {dishes.filter(({ category }) => category === Category.SIDE).map(dish => (
        <Col key={dish.title} className="mt-3 mb-3" lg={4} md={6}>
          <Dish {...dish} />
        </Col>
      ))}
    </Row>

    <p>Ready to order? Have a question? Call or text us on <AriesTelephoneLink /> and we're happy to help you!</p>
  </Layout>
)

function Dish({ image, title, options, tags, price, ...props }) {
  const data = useStaticQuery(graphql`
    query {
      images: allFile(filter: {
        sourceInstanceName: { eq: "images" }
      }) {
        edges {
          node {
            relativePath,
            childImageSharp {
              fluid(quality: 90) {
                ...GatsbyImageSharpFluid,
              }
            }
          }
        }
      }
      placeholder: file(relativePath: { eq: "store-front.jpg" }) {
        childImageSharp {
          fluid(quality: 90) {
            ...GatsbyImageSharpFluid,
          }
        }
      }
    }`);

  const edge = data.images.edges.find(({ node }) => node.relativePath === image);
  const fluid = edge === undefined ? data.placeholder.childImageSharp.fluid : edge.node.childImageSharp.fluid;
  return (
    <>
      <Img className="mb-3 rounded" sizes={{ ...fluid, aspectRatio: 4/3 }} alt={title} />
      <h5>
        {title}{" "}
        <LightPrice value={price} />
      </h5>
      <div>
        {
          tags.map((tag, index) => [
            index > 0 && " ",
            <Badge key={tag} variant="info">{tag}</Badge>,
          ])
        }
        <ul>
          { options.map(({ title, price }, index) => (
            <li key={index}>{title} <LightPrice value={price} /></li>
          )) }
        </ul>
      </div>
    </>
  );
}

export default MenuPage
